.news{
  margin-top: 58px;

  &__link{
    margin-top: 16px;
  }

  &__list{
    margin-top: 62px;
    display: grid;
    grid-template-columns: repeat(12,1fr);
    column-gap: 16px;
    row-gap: 9px;

  }

  .card-news--big{
    grid-column: 10 span;
    grid-row: 4 span;
    margin: 0;
  }
}

@media (max-width: 1500px){

  .news{

    &__item:first-child{
      grid-column: 12 span;
      grid-row: 1 span;
    }

    &__item{
      grid-column: 3 span;

      img{
        width: 100%;
      }
    }
  }

}

@media (max-width: 800px){

  .news{

    &__item:last-child{
      display: none;
    }

    &__item{
      grid-column: 4 span;
    }
  }
}


@media (max-width: 525px){

  .news{
    margin-top: 30px;
    flex-direction: column;

    &__list{
      margin-top: 25px;
    }

    &__item{
      grid-column: 6 span;
    }

    &__item:nth-last-child(-n + 2){
      display: none;
    }
  }

}
