.news {
  margin-top: 58px;
}
.news__link {
  margin-top: 16px;
}
.news__list {
  margin-top: 62px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 16px;
  row-gap: 9px;
}
.news .card-news--big {
  grid-column: 10 span;
  grid-row: 4 span;
  margin: 0;
}

@media (max-width: 1500px) {
  .news__item:first-child {
    grid-column: 12 span;
    grid-row: 1 span;
  }
  .news__item {
    grid-column: 3 span;
  }
  .news__item img {
    width: 100%;
  }
}
@media (max-width: 800px) {
  .news__item:last-child {
    display: none;
  }
  .news__item {
    grid-column: 4 span;
  }
}
@media (max-width: 525px) {
  .news {
    margin-top: 30px;
    flex-direction: column;
  }
  .news__list {
    margin-top: 25px;
  }
  .news__item {
    grid-column: 6 span;
  }
  .news__item:nth-last-child(-n+2) {
    display: none;
  }
}